body {
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: .01em;
  scroll-behavior: smooth;
  background: #0a0e14 center / cover no-repeat fixed;
  min-height: 100vh;
  margin: 0;
  padding-top: 200px;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  font-weight: 400;
  line-height: 1.7;
  transition: background-image .8s;
  position: relative;
  overflow-x: hidden;
}

body:before {
  content: "";
  z-index: 0;
  pointer-events: none;
  background: linear-gradient(135deg, #0a0e14eb 0%, #141c24e0 50%, #1e2a36d9 100%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  inset: 0;
}

.background-overlay {
  z-index: 0;
  opacity: .6;
  pointer-events: none;
  filter: brightness(.8) contrast(1.1);
  background: #181a2073 center / cover no-repeat;
  width: 100vw;
  height: 100vh;
  transition: background-image .8s, opacity .8s;
  display: none;
  position: fixed;
  inset: 0;
}

.loading-overlay {
  z-index: 2000;
  backdrop-filter: blur(8px);
  background: #000000b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.loading-content {
  backdrop-filter: blur(20px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 20px;
  padding: 30px 40px;
  box-shadow: 0 8px 32px #0000004d;
}

.loading-spinner {
  border: 3px solid #ffffff4d;
  border-top-color: #fff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 0 auto 20px;
  animation: 1s linear infinite spin;
}

.loading-text {
  color: #fff;
  letter-spacing: .5px;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 500;
}

.error-message-overlay {
  backdrop-filter: blur(10px);
  z-index: 3000;
  background: #dc3545f2;
  border: 1px solid #fff3;
  border-radius: 12px;
  align-items: center;
  gap: 12px;
  min-width: 300px;
  max-width: 400px;
  padding: 16px 24px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 8px 32px #dc35454d;
}

.error-message-content {
  align-items: center;
  gap: 12px;
  width: 100%;
  display: flex;
}

.error-icon {
  flex-shrink: 0;
  font-size: 20px;
}

.error-text {
  color: #fff;
  letter-spacing: .2px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
}

.background-spinner {
  z-index: 1000;
  background: #1418242e;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.background-spinner .spinner {
  border: 4px solid #fff;
  border-top-color: #1976d2;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#app {
  width: 100%;
  max-width: 100%;
  min-height: 100vh;
  box-shadow: none;
  z-index: 1;
  flex-direction: column;
  margin: 0;
  padding: 0 0 400px;
  display: flex;
  position: relative;
  overflow-x: hidden;
  background: none !important;
}

.header {
  color: #fff;
  z-index: 1000;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 24px 32px 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header-top {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin-bottom: 24px;
  padding: 0 16px;
  display: flex;
}

.header-left {
  align-items: center;
  display: flex;
}

.app-title {
  letter-spacing: -.02em;
  text-shadow: 0 4px 12px #0000004d;
  color: #fff;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #fff 0%, #e2e8f0 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0;
  font-size: 2.4rem;
  font-weight: 800;
}

.header-right {
  align-items: center;
  display: flex;
}

.language-selector-header {
  position: relative;
}

.language-btn-header {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  font-size: 14px;
  font-weight: 500;
  transition: all .3s;
  display: flex;
  box-shadow: 0 2px 8px #0000001a;
}

.language-btn-header:hover {
  background: #ffffff26;
  border-color: #ffffff4d;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #00000026;
}

.language-flag {
  font-size: 16px;
}

.language-name {
  font-weight: 600;
}

.language-arrow {
  font-size: 10px;
  transition: transform .3s;
}

.language-btn-header.active .language-arrow {
  transform: rotate(180deg);
}

.language-dropdown-header {
  backdrop-filter: blur(20px);
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background: #fffffff2;
  border: 1px solid #fff3;
  border-radius: 12px;
  min-width: 200px;
  transition: all .3s;
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  transform: translateY(-10px);
  box-shadow: 0 8px 32px #00000026;
}

.language-dropdown-header.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.language-option-header {
  color: #333;
  cursor: pointer;
  border-bottom: 1px solid #0000000d;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  font-weight: 500;
  transition: all .2s;
  display: flex;
}

.language-option-header:last-child {
  border-bottom: none;
}

.language-option-header:hover {
  color: #1976d2;
  background: #3b82f61a;
}

.language-option-header .language-flag {
  font-size: 18px;
}

.language-option-header .language-name {
  font-weight: 600;
}

.header-title {
  letter-spacing: -.04em;
  text-shadow: 0 8px 16px #0006;
  color: #fff;
  text-align: center;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #fff 0%, #e2e8f0 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 24px;
  font-size: 3.2rem;
  font-weight: 900;
}

.header-search {
  gap: 8px;
  margin-bottom: 8px;
  display: flex;
}

.header-search input {
  border: none;
  border-radius: 4px;
  padding: 6px 10px;
  font-size: 1rem;
}

.header-search button {
  color: #1976d2;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 14px;
  font-weight: bold;
  transition: background .2s;
}

.header-search button:hover {
  background: #e3eaf2;
}

.header-cities {
  gap: 8px;
  padding-bottom: 4px;
  display: flex;
  overflow-x: auto;
}

.header-city {
  cursor: pointer;
  white-space: nowrap;
  background: #fff3;
  border-radius: 12px;
  padding: 4px 10px;
  transition: background .2s;
}

.header-city.selected, .header-city:hover {
  color: #1976d2;
  background: #fff;
}

.nav-btn {
  color: #fff;
  letter-spacing: .02em;
  cursor: pointer;
  backdrop-filter: blur(20px);
  background: #ffffff14;
  border: 1px solid #ffffff1f;
  border-radius: 12px;
  min-width: 100px;
  margin: 8px 6px 0;
  padding: 14px 28px;
  font-size: .9rem;
  font-weight: 600;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 2px 8px #0000001a;
}

.nav-btn.active, .nav-btn:hover {
  color: #fff;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border-color: #3b82f64d;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #3b82f666;
}

.main-layout {
  justify-content: space-between;
  align-items: flex-start;
  gap: 40px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
