}

.weather-right-group {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
  display: flex;
  position: relative;
}

.quote-section {
  backdrop-filter: blur(20px);
  text-align: center;
  z-index: 100;
  background: #ffffff14;
  border: 1px solid #ffffff1f;
  border-radius: 16px;
