  line-height: 1.6;
}

.quote-author {
  color: #ffffffb3;
  font-size: .9rem;
  font-weight: 400;
}

.search-input-wrapper {
  background: #fffffff2;
  border-radius: 25px;
  transition: all .3s;
  position: relative;
  box-shadow: 0 4px 16px #0000001a;
}

.search-input-wrapper:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 20px #00000026;
}

.search-input-wrapper:focus-within {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #3b82f633;
}

.quick-countries {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  margin-top: 12px;
  display: flex;
}

.quick-country-btn {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 13px;
  font-weight: 500;
  transition: all .3s;
}

.quick-country-btn:hover {
  background: #fff3;
  border-color: #ffffff4d;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #0003;
}

#main-content {
  padding: 12px 16px 20px;
}

.today-weather {
  text-align: center;
}

.today-weather h2 {
  margin-bottom: 8px;
}

.weather-main {
  justify-content: center;
  align-items: center;
  gap: 18px;
  display: flex;
}

.weather-info {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.temp {
  font-size: 2.5rem;
  font-weight: bold;
}

.desc {
  text-transform: capitalize;
  color: #555;
  font-size: 1.1rem;
}

.five-days-forecast {
  backdrop-filter: blur(44px);
  background: linear-gradient(135deg, #0f172afa 0%, #1e293bf2 100%);
  border: 1px solid #ffffff26;
  border-radius: 40px;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  max-width: 100%;
  margin: 50px 60px;
  padding: 56px 56px 52px;
  display: flex;
  box-shadow: 0 32px 88px #00000073, 0 16px 40px #3b82f64d;
}

.forecast-cards {
  width: 100%;
  box-shadow: none;
  background: none;
  border-radius: 0;
  grid-template-columns: repeat(5, 1fr);
  gap: 36px;
  margin-bottom: 20px;
  padding: 0;
  display: grid;
  position: relative;
}

.forecast-card {
  word-break: break-word;
  backdrop-filter: blur(24px);
  background: #0f172ae6;
  border: 1px solid #ffffff26;
  border-radius: 28px;
  flex-direction: column;
  align-items: center;
  min-width: 160px;
  max-width: 200px;
  margin: 0;
  padding: 24px 20px 20px;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 16px 40px #0000004d, 0 6px 16px #3b82f633;
}

.forecast-card:not(:first-child):before, .forecast-card:not(:last-child):after {
  display: none;
}

.forecast-card[data-expanded="true"], .forecast-card:hover {
  z-index: 2;
  background: #0f172ae6;
  border: 2px solid #3b82f6;
  transform: translateY(-6px)scale(1.02);
  box-shadow: 0 16px 48px #3b82f633, 0 4px 16px #3b82f626;
}

.forecast-day {
  color: #fff;
  letter-spacing: .02em;
  text-shadow: 0 2px 4px #00000026;
  margin-bottom: 4px;
  font-size: .8rem;
  font-weight: 600;
  transition: color .3s;
}

.forecast-day.today {
  color: #3b82f6;
  text-shadow: 0 2px 8px #3b82f64d;
  font-weight: 700;
}

.forecast-date {
  color: #f1f5f9;
  letter-spacing: .02em;
  opacity: .95;
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 600;
}

.forecast-card img {
  filter: brightness(1.2) drop-shadow(0 4px 8px #0003);
  width: 48px;
  height: 48px;
  margin-bottom: 16px;
}

.forecast-minmax {
  color: #fff;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 0;
  margin-bottom: 8px;
  font-size: .65rem;
  line-height: 1.2;
  display: flex;
  position: relative;
  box-shadow: none !important;
  background: none !important;
  border: none !important;
}

.minmax-divider {
  opacity: .45;
  background: linear-gradient(#ffd54f 0%, #fffde7 100%);
  border-radius: 1px;
  align-self: center;
  width: 1px;
  height: 20px;
  margin: 0 4px;
  display: inline-block;
}

.forecast-minmax span {
  color: #ffb74d;
  font-size: .65rem;
  font-weight: 700;
}

.forecast-desc {
  text-transform: capitalize;
  color: #e0e0e0;
  opacity: .88;
  margin-top: 8px;
  font-size: .72rem;
}

.more-info-btn {
  color: #fff;
  letter-spacing: .2px;
  text-shadow: 0 1px 4px #0000002e;
  background: #ffffff0a;
  border: 1.5px solid #ffffff21;
  border-radius: 16px;
