}

.more-info-btn:hover, .more-info-btn:focus {
  color: #ffb74d;
  text-shadow: none;
  background: #ffffff1a;
  border: 1.5px solid #ffb74d;
}

.more-info-section {
  color: #fff;
  text-align: left;
  border-top: 1.5px solid #ffffff1a;
  border-radius: 14px;
  flex-flow: row;
  justify-content: flex-start;
