  font-size: .92rem;
  display: flex;
  overflow: auto hidden;
  box-shadow: 0 2px 8px #00000014;
  background: #181a20 !important;
}

.more-info-mini-card {
  text-align: center;
  color: #fff;
  background: #242834fa;
  border: 1px solid #ffffff14;
  border-radius: 10px;
  flex-direction: column;
  flex: 1 1 0;
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  min-width: 80px;
  max-width: 100px;
  height: 145px;
  margin: 0 6px 12px;
  padding: 6px 2px 4px;
  font-size: .72rem;
  transition: box-shadow .18s, border .18s;
  display: flex;
  box-shadow: 0 1.5px 6px #1976d20f, 0 1px 4px #00000014;
}

.more-info-mini-card .mini-time {
  color: #ffd54f;
  letter-spacing: .2px;
  margin-bottom: 2px;
  font-size: .62rem;
