  display: flex;
  box-shadow: 0 4px 16px #00000024, 0 1px 4px #1976d20f;
  width: 100% !important;
  max-width: 900px !important;
  padding: 24px 24px 20px !important;
}

.forecast-chart {
  background: none;
  border-radius: 10px;
  margin: 0 auto;
  transition: box-shadow .2s;
  display: block;
  width: 100% !important;
  min-width: 560px !important;
  max-width: 820px !important;
  height: 280px !important;
}

.forecast-chart text {
  letter-spacing: .5px !important;
  font-size: 1.5rem !important;
  font-weight: 700 !important;
}

.header-blur {
  backdrop-filter: none;
  max-width: none;
  box-shadow: none;
  background: none;
  border-radius: 0;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.header-searchbar {
  backdrop-filter: blur(20px);
  z-index: 2;
  background: linear-gradient(135deg, #fffffffa 0%, #fffffff2 100%);
  border: 1px solid #ffffff40;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  gap: 12px;
  width: 100%;
  max-width: 1000px;
  margin: 16px auto 0;
  padding: 10px 16px;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
  box-shadow: 0 8px 32px #0000001f, 0 2px 8px #00000014, 0 0 0 1px #ffffff1a;
}

.header-searchbar:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 40px #00000026, 0 4px 12px #0000001a, 0 0 0 1px #fff3;
}

@media (width <= 900px) {
  .header-searchbar {
    gap: 8px;
    width: 100%;
    max-width: 90vw;
    padding: 8px 12px;
  }

  .search-input-wrapper {
    max-width: 100%;
  }

  .country-dropdown-container {
    width: 90vw !important;
    max-width: 90vw !important;
  }
}

.search-input-wrapper {
  background: #fff;
  border: 1px solid #00000014;
  border-radius: 12px;
  align-items: center;
  width: 100%;
